* {
  font-family:"Montserrat",sans-serif
  
}

body {
  background-color:  #efefef;
  margin: 0;
}

h2 {
  font-size: 2vw;
}

.mdi {
  margin-right: 1vw;
}

h3 {
  font-size: 1.2vw;

}

p  {
  font-size: 2vw;
  line-height: 8px;
}

nav {
  background-color: #110a49;
  display: flex;
  flex-flow: row nowrap;
  gap: 200px;
}
nav h2 {
  color: white;
  margin-left: 350px;
}
nav ul {
  display: flex;
  flex-flow: row nowrap;
  list-style-type: none;
  gap: 30px;
  align-items: center;
  font-size: 1.5vw;

}
li {
  color: white;
}
a {
  text-decoration: none;
  color: white;
}

a:visited {
  color: white;
}

.products {
  display: flex;
  flex-flow: row wrap;
  gap: 100px;
  width: 900px;
margin: 15vh auto 30vh auto;
align-items: center;

}

.add {

font-size: 1.2vw;
padding: 5px;
font-weight: bold;
margin-top: 10px;
width: 100%;
background-color:  #2d8cec;
color: rgb(255, 255, 255);

}

h2.products-title {
text-align: center;
}
.product {
  box-shadow: 0 2px 9px rgba(0,0,0,.35);
}

hr {
width: 300px;

margin-bottom: 0;
  color: grey;
  height: 1px;
}

p.prod-copy {
margin-top: 80%;
  font-size: 1.1vw;
  font-weight: 700;
}

p.prod-price {
  font-size: 1.1vw;
  font-weight: 700;
}

.cart {
  position: fixed;
z-index: 1;
top: 0px;
display: none;
flex-direction: column;
-moz-box-align: center;
align-items: center;
gap: 2rem;
width: 20%;
height: 100%;
padding: 3rem;
background-color: rgb(255, 255, 255);
font-size: 3rem;
transition: right 0.85s ease-in-out 0s;
right: 0px;
text-align: center;
}

.blur {
  position: fixed;
top: 0px;
width: 100%;
height: 100%;
background-color: black;
opacity: 0.6;
transition: left 0.85s ease-in-out 0s;
left: 0px;
}

.trolley {
  height: 260px;
  width: 330px;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
overflow: auto;
column-gap: 10px;
padding: 1.5rem;
}

p.product-title {
  font-size: 20px;
  font-weight: 700;
}
p.product-price {
  font-size: 15px;
  font-weight: 700;

}
.pricing {
  display: flex;
  flex-flow: row nowrap;
}

.pricing p {
  font-size: 15px;
  font-weight: 700;
}

.total-pricing {
  display: flex;
  flex-flow: row nowrap;
}
.total-pricing h3 {
  font-size: 1.2vw;
}
.meta {
  height: 250px;
width: 150px;
display: flex;
flex-flow: column nowrap;
align-items: center;
}

.meta p {
  margin-bottom: 5px;
  line-height: 20px;
  text-align: center;
}

.product-in-cart {
min-height: 200px;
min-width: 170px;
display:  flex;
flex-flow: column nowrap;
align-items: center;
  background-repeat: no-repeat;
  background-position: top;
  background-size: contain;
  margin-bottom: 0;

}
button.remove-item {
  margin-top: 80%;
}


.quantity {
  display: flex;
  flex-flow: row nowrap;
  width: 100%;
  gap: 20px;
  justify-content: center;
  align-items: center;
  margin-top: 5px;

}

button.reduce, button.increase {
  width: 25px;
  height: 25px;
  font-weight: bold;
  background-color: rgb(212, 212, 212);
  border: none;
}

#number {
  font-size: 15px;
}

p#sub-total {
  font-size: 15px;
}

